body {
  box-sizing: border-box;
  margin: 0px;
}

button {
  display: block;
  padding: 10px 20px;
  font-size: 25px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: #2FBFEC;
  border: none;
  border-radius: 25px;
  margin: 40px auto 10px auto;
  width: 80%;
  font-family: 'Concert One';
}

.replay-button {
  margin-top: 5px;
  background-color: #1BBB00;
}

.play-button {
  /* Disable double tap zoom */
  touch-action: manipulation;
}

button:hover {background-color: #283655}

button:active {
  background-color: #283655;
/*   box-shadow: 0 2px #666; */
  transform: translateY(3px);
}

button:disabled {
  background-color: #777879;
  color: #BDBFBB;
}

.main-container {
 width: 100%;
}

.top-bar {
  background: url('top-bar-bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  height: 120px;
}

/****** round container *******/

.round {
  margin: -45px auto 10px auto;
  height: 180px;
  width: 180px;
  border-radius: 50%;
  background-color: #293F5C;
  color: white;
  text-align: center;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.round-top-title {
  padding-top: 10px;
  height: 60px;
  font-size: 30px;
  font-family: 'Concert One';
  display: flex;
  align-items: center; 
  justify-content: center;
}

.round-middle-title {
  margin-top: 0;
  padding-top: 0;
  font-size: 60px;
  height: 60px;
  font-family: 'Concert One';
  display: flex;
  align-items: center; 
  justify-content: center;
}

.round-bottom-title {
  padding-bottom: 10px;
  height: 60px;
  font-size: 30px;
  display: flex;
  align-items: center; 
  justify-content: center;
  font-family: 'Concert One';
}

/******* Message containers ******/

.result-message-container {
  width: 80%;
  margin: 20px auto 10px auto;
  font-size: 25px;
  height: 50px;
  text-align: center;
  font-family: 'Georgia', 'serif';
  color: #3A3A3B;
}

.clicks-message-container {
  font-family: 'Concert One';
  width: 80%;
  margin: 30px auto 10px auto;
  height: 50px;
  font-size: 15px;
  text-align: center;
  color: #777879;
}

@media only screen and (min-width: 441px) {
  .main-container {
    width: 440px;
    margin: 30px auto;
    border: solid 1px #777879;
    border-radius: 30px;
    box-shadow: -3px 9px 16px -10px rgba(0,0,0,0.68);
  }


.top-bar {
  border-radius: 30px 30px 0 0;
}

  .buttons-container {
    height: 200px;
  }
}

